import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { DataProvider } from '../../providers/data/data';
import { Collections, Content, Event, User } from '../../app/app.models';
import { AuthProvider } from '../../providers/auth/auth';
import { AngularFirestore } from 'angularfire2/firestore';

@IonicPage({
  segment: 'tema-alt/:id'
})
@Component({
  selector: 'page-tema-alt',
  templateUrl: 'tema-alt.html',
})
export class TemaAltPage {

  public id: string = this.navParams.get('id');

  public tema: Content;
  public children: Content[];
  public event: Event;

  public completedTasks: string[] = [];
  private loaded = false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private data: DataProvider,
    private auth: AuthProvider,
    private afs: AngularFirestore,
  ) { }

  ionViewDidLoad() {

    this.loadContent(this.id);
    this.loadChildren(this.id);

    this.auth.user$.subscribe(user => {
      if (this.loaded) return;
      if (!user) { return; }
      const userKey = `${Collections.USER}/${user.uid}`;
      this.afs.doc<User>(userKey).valueChanges().subscribe(_user => {
        if (!_user.completedTasks) { return; }
        this.completedTasks = user.completedTasks;
      });
      this.loaded = true;
    });

  }

  async loadContent(id: string) {
    this.tema = await this.data.getDocAlt<Content>(Collections.CONTENT, id);
    if (this.tema && this.tema.event) this.loadEvent(this.tema.event)
  }

  async loadChildren(id: string) {
    const children = await this.data.getCollectionQuery<Content>(
      Collections.CONTENT,
      ref => ref.where('parent_id', '==', id)
    );
    this.children = children.filter(c => c.liberadoInPrograma);
  }

  async loadEvent(id: string) {
    this.event = await this.data.getDocAlt<Event>(Collections.EVENT, id);
    console.log(this.event);
  }

  openLink(url: string) {

    console.log(url);

    // Content Detail
    if (url.includes('/content')) {
      const [,, type, id] = url.split('/');
      this.navCtrl.push('ClaseDetailPage', {type, id});
    }

  }

  async toggleCompleted(id: string) {

    console.log(id)

    const userKey = `${Collections.USER}/${this.auth.user.uid}`
    const _user = await this.data.getDocAlt<User>(Collections.USER, this.auth.user.uid);
    const completedTasks: string[] = _user.completedTasks || []

    if (completedTasks.indexOf(id) < 0) {

      completedTasks.push(id)
      await this.afs.doc(userKey).update({ completedTasks })
      /* this.checkChanged.next({ id, added: true }); */

    } else {
      if (completedTasks.length > 0) completedTasks.splice(completedTasks.indexOf(id), 1)
      await this.afs.doc(userKey).update({ completedTasks })
      /* this.checkChanged.next({ id, added: false }); */
    }

    // this.stats.modifyCustomCounter(`event-${id}`, this.event.title, 1)

  }

}



